import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BlogService } from '../blog.service';
import { UserService } from '../user.service';
import { NgForm } from '@angular/forms';
import { NotificationComponent } from '../notification/notification.component';


declare const $: any;
declare const tinymce: any;
declare const webExtensionWallet: any;


@Component({
  providers: [NotificationComponent],
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css']
})

export class PostComponent implements OnInit {
  timer: any;
  extProm = false;
  catNames = ["区块链", ".NET技术", "编程语言", "软件设计", "Web前端", "企业信息化",
              "手机开发", "软件工程", "数据库技术", "操作系统", "其他分类", "所有随笔"];

  selectedCatIndex = 0;

  constructor(private blogService: BlogService,
              private notification: NotificationComponent,
              private router: Router,
              private userService: UserService) { }

  ngOnInit() {

  }

  ngAfterViewInit () {
    var that =this;

    tinymce.init({
      selector: 'textarea',
      height: 500,
      plugins: 'print preview fullpage searchreplace autolink directionality visualblocks visualchars fullscreen image link media template codesample table charmap hr pagebreak nonbreaking anchor toc insertdatetime advlist lists textcolor wordcount imagetools contextmenu colorpicker textpattern help',
      toolbar1: 'formatselect | bold italic strikethrough forecolor backcolor | link | alignleft aligncenter alignright alignjustify  | numlist bullist outdent indent  | removeformat',
      image_advtab: true,
      language: 'zh_CN'
    });

    this.timer = setTimeout(function () {
      if (typeof(webExtensionWallet) === 'undefined') {
        // $("#extProm").show();
        // $(".blog.segment").removeClass("loading");
        that.extProm = true;
        window.clearTimeout(that.timer)
      }
    }, 1000);
  }

  onPostBlog(blogForm: NgForm) {
    const content = tinymce.activeEditor.getContent();

    this.userService.getAuthor().subscribe(value => {
      if (value == 'null') {
        this.notification.show("请先注册");
        $(".ui.register.sidebar").sidebar("toggle");
      } else {
        if (blogForm.valid && content) {
          let params = blogForm.value;

          params.cat = this.catNames[this.selectedCatIndex];
          params.content = content;
          $("#blogForm").addClass('loading');
          this.blogService.createPost(params).subscribe(
            value => {},
            error => {
              this.notification.show(error);
              $("#blogForm").removeClass('loading');
              $(".ui.register.sidebar").sidebar("toggle");
            },
            () => {
              window.location.href = "/mine";
            }
          );
        }
      }
    }, error => {
      this.notification.show(error);
    });
  }
}



