import { Component, OnInit, ApplicationRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import { BlogService } from '../blog.service';
import { NotificationComponent } from '../notification/notification.component';




declare const $:any;

@Component({
  providers: [NotificationComponent],
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.css']
})
export class BlogComponent implements OnInit {
  blog: any;
  payItems = [];

  constructor(private blogService: BlogService,
              private ref: ApplicationRef,
              private notification: NotificationComponent,
              private router: ActivatedRoute) { }

  ngOnInit() {
    this.blogService.getPostById(this.router.snapshot.params["id"]).subscribe(val => {
      this.blog = JSON.parse(val);
      document.title = this.blog.title;
      this.payItems = this.blog.payItems;
      this.ref.tick();
    });
  }

  ngAfterViewInit() {
    window.scrollTo(0, 0);
  }

  show(data) {
    let payItems = data.payItems,
      commentsHtml = "";

    payItems.forEach(function (item) {
      let html = "<div class='comment'><a class='avatar'><img src='https://api.adorable.io/avatars/285/'" + item.from + ".png></a>" +
          "<div class='content'><a class='author'>" + item.fromName + "</a><div class='metadata'><span class='date'>" + "</span></div>" +
          "<div class='text'>" + item.comment + "</div></div></div>";

      commentsHtml += html;
    });

    $("#blogDetail .all-comment").html(commentsHtml);
    $("#blogDetail .header .title").html(data.title);
    $("#blogDetail .blog.content").html(data.content);
    $("#blogDetail input[type=hidden]").val(data.pid);
    $("#blogDetail").modal('show');
  }

  hide() {
    $("#blogDetail").modal('hide');
  }

  onSendPay(sendPayForm: NgForm, blog, event) {
    if (sendPayForm.valid) {
      let params = sendPayForm.value,
        target = $(event.target),
        payItems = this.payItems;

      params.pid = blog.pid;
      target.addClass("loading");
      this.blogService.sendPay(params).subscribe(
        value => {
          sendPayForm.reset();
          target.removeClass("loading");
          params.from = value.from;
          params.ct = new Date().getTime();
          payItems.push(params);
          this.ref.tick();
        },
        error => {
          this.notification.show(error);
          target.removeClass("loading");
        },
        () => {}
      )
    }
  }
}



